import React, { useState, useEffect } from "react";
import "./form.css";
import events from "./event";
import { useInput } from "./form-hook";
import axios from "axios";

function Form() {
  const [registered, setRegistered] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState("");
  const [eventTimings, setEventTimings] = useState([]);
  const [selectedTime, setSelectedTime] = useState("");

  const { value: name, bind: bindName, reset: resetName } = useInput("");
  const { value: email, bind: bindEmail, reset: resetEmail } = useInput("");
  const { value: phone, bind: bindPhone, reset: resetPhone } = useInput("");
  const { value: hear, bind: bindHear, reset: resetHear } = useInput("");
  const {
    value: organisation,
    bind: bindOrganisation,
    reset: resetOrganisation,
  } = useInput("");
  const { value: payment, bind: bindPayment, reset: resetPayment } = useInput(
    ""
  );

  const handleSubmit = (event) => {
    event.preventDefault();
    const body = {
      name,
      email,
      phone,
      hear,
      organisation,
      selectedEvent,
      selectedTime,
      payment,
    };

    axios
      .post("http://localhost:5000/mail", body)
      .then((response) => {
        if (response.data.success === true) {
          setRegistered(true);
        } else {
          setRegistered(false);
        }
      })
      .catch((err) => console.log(err));
    resetName();
    resetEmail();
    resetPhone();
    resetHear();
    resetOrganisation();
    resetPayment();
  };

  useEffect(() => {
    setSelectedTime("");
  }, [selectedEvent]);

  const handleChangeEvent = (e) => {
    setSelectedEvent(e.target.value);
    const filterEvent = events.filter(
      (exactEvent) => exactEvent.name === e.target.value
    );
    setEventTimings(filterEvent[0].timings);
  };

  const handleChangeTiming = (e) => {
    setSelectedTime(e.target.value);
  };

  return (
    <section>
      <div className="container">
        <h3 className="mb-5">Register</h3>
        <div className="row">
          <div className="col">
            <form onSubmit={handleSubmit}>
              <input
                type="text"
                className="form-control mb-3"
                placeholder="Name"
                aria-label="Name"
                required
                {...bindName}
              />
              <input
                type="email"
                className="form-control mb-3"
                placeholder="Email"
                aria-label="Email"
                required
                {...bindEmail}
              />
              <input
                type="phone"
                className="form-control mb-3"
                placeholder="Phone"
                aria-label="Phone"
                required
                {...bindPhone}
              />
              <input
                type="text"
                className="form-control mb-3"
                placeholder="How did you hear about us?"
                aria-label="How did you hear about us?"
                required
                {...bindHear}
              />
              <input
                type="text"
                className="form-control mb-3"
                placeholder="Organization"
                aria-label="Organization"
                required
                {...bindOrganisation}
              />
              <select
                className="form-select mb-3"
                onChange={handleChangeEvent}
                value={selectedEvent}
                required
              >
                <option value="" disabled />
                {events.map((event, index) => (
                  <option key={index} value={event.name}>
                    {event.name}
                  </option>
                ))}
              </select>
              <select
                className="form-select mb-3"
                onChange={handleChangeTiming}
                value={selectedTime}
                disabled={eventTimings.length <= 0 ? true : false}
              >
                <option value="" disabled />
                {eventTimings.map((time, index) => (
                  <option key={index} value={time}>
                    {time}
                  </option>
                ))}
              </select>
              <div className="form-check mb-3">
                <input
                  className="form-check-input mb-3 shadow-none"
                  type="checkbox"
                  required
                  id="payment"
                  {...bindPayment}
                />
                <label className="form-check-label" htmlFor="payment">
                  I understand that I will have to pay Rs.1499 per workshop **
                </label>
              </div>
              <div>
                <p>UPI ID: shwetajain@okhdfcbank</p>
              </div>
              {registered ? (
                <div className="alert alert-success mt-5 mb-5">
                  Registered Successfully, Pay now, through UPI Id.
                </div>
              ) : (
                " "
              )}
              <button className="btn btn-primary shadow-none">Register</button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Form;
